/*------ Vectormap-----*/
.jvectormap-container {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
  }
  
  .jvectormap-tip {
	position: absolute;
	display: none;
	border-radius: 3px;
	background: #212529;
	color: white;
	padding: 6px;
	font-size: 11px;
	line-height: 1;
	font-weight: 700;
  
	small {
	  font-size: inherit;
	  font-weight: 400;
	}
  }
  
  .jvectormap-zoomin, .jvectormap-zoomout, .jvectormap-goback {
	position: absolute;
	inset-inline-start: 10px;
	border-radius: 3px;
	background: #292929;
	padding: 3px;
	color: white;
	cursor: pointer;
	line-height: 10px;
	text-align: center;
	box-sizing: content-box;
  }
  
  .jvectormap-zoomin, .jvectormap-zoomout {
	width: 10px;
	height: 10px;
  }
  
  .jvectormap-zoomin {
	inset-block-start: 10px;
  }
  
  .jvectormap-zoomout {
	inset-block-start: 30px;
  }
  
  .jvectormap-goback {
	inset-block-end: 10px;
	z-index: 1000;
	padding: 6px;
  }
  
  .jvectormap-spinner {
	position: absolute;
	inset-inline-start: 0;
	inset-block-start: 0;
	inset-inline-end: 0;
	inset-block-end: 0;
	background: center no-repeat url(data:image/gif;base64,);
  }
/*------ Vectormap End-----*/