		background-color: rgba($success, 0.8) !important;
		border-color: rgba($success, 0.8) !important;
	}

	&.disabled,
	&:disabled {
		color: $white;
		background-color: #42c48a;
		border-color: #42c48a;
	}

	&:not(:disabled):not(.disabled) {

		&:active,
		&.active {
			color: $white;
			background-color: #42c48a;
			border-color: #42c48a;
		}
	}
}

.show>.btn-success.dropdown-toggle {
