}

.btn-lg,
.btn-group-lg>.btn {
	padding: 0.7rem 2rem;
	font-size: 1.125rem;
	line-height: 1.625;
	border-radius: 3px;
	font-size: 16px;
	min-width: 2.75rem;
}

.btn-sm,
.btn-group-sm>.btn {
	font-size: 0.711rem;
	border-radius: 0.21rem;
	padding: 0.25rem 0.5rem;
	min-width: 1.625rem;
	line-height: 1.5;
}

.btn-block {
	display: block;
	width: 100%;

	+.btn-block {
		margin-block-start: 0.5rem;
	}
}

input {

	&[type="submit"].btn-block,
	&[type="reset"].btn-block,
	&[type="button"].btn-block {
		width: 100%;
	}
}

.fade {
	transition: opacity 0.15s linear;

	&:not(.show) {
		opacity: 0;
	}
}

.collapse:not(.show) {
	display: none;
	background: $white;
}

.collapsing {
	position: relative;
	height: 0;
	overflow: hidden;
	transition: height 0.35s ease;
}

.btn-icon {
	padding-inline-start: .5rem;
	padding-inline-end: .5rem;
	text-align: center;
}

.btn-pill {
	border-radius: 10rem;
}

.btn-square {
	border-radius: 2px;
}

